@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,600);
body {
  background: #fafafa;
}

.main {
  width: 100%;
  padding-left: 300px;
  position: absolute;
  top: 15px;
}

.photo {
  width: 35px;
  height: 35px;
}

@media only screen and (max-width: 992px) {
  .main {
    padding-left: 0;
    top: 70px;
  }
}

.top-nav-logos {
  max-width: 100vw;
  height: 120px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-text a:not(.btn) {
  color: #757575 !important;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 10pt;
}

.general-form__header {
  font-weight: bold;
  color: #757575;
  margin-bottom: 15px;
}

.general-form__reset {
  vertical-align: bottom;
}

.arrow {
  vertical-align: bottom;
}

.user-info {
  color: #757575 !important;
}

.user-info__field {
  height: 50px;
}

.preloading {
  position: fixed;
  top: 0;
  left: 0;
  background: #fafafa;
  width: 100vw;
  height: 100vh;
  z-index: 10;
}

#delete-group-modal {
  width: 300px !important;
}
